@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 80%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(197, 222, 223);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 1300px) {
  html {
    font-size: 75%;
  }
}

main {
  font-family: "Poppins", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 20vh;
  padding-bottom: 20vh;
  width: 100%;
}

header {
  padding: 0.5rem 1.6rem;
  margin-bottom: 4rem;
  font-size: 5rem;
  text-transform: uppercase;
  letter-spacing: 1rem;
  border: 0.5rem solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-frame {
  display:flex;
  flex-direction: column;
  align-items: center;
  width: 100rem;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.main-section {
  padding: 4rem 0rem;
  width: 100%;
  background: white;
}

@media screen and (max-width: 1300px) {
  .main-frame {
    width: 90%;
  }
  header {
    font-size: 4rem;
  }
}

@media screen and (max-width: 714px) {
  .main-frame {
    width: 100%;
  }
  header {
    font-size: 3rem;
  }
}
nav {
  width: 100%;
  height: 6rem;
  background: rgba(255, 255, 255, 0.33);
}

.nav-links {
  display: flex;
  justify-content: space-around;
  font-size: 1.3rem;
  letter-spacing: 0.4rem;
  width: 50rem;
  height: 100%;
}

.nav-links .link {
  text-decoration: none;
  color: black;
  background: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
  outline: none;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.link.active {
  background: white;
}

.nav-links .link:hover {
  background: rgba(255, 255, 255, 0.5);
}

.link.active:hover {
  color: rgb(50, 200, 200);
  background: white;
}

.burger {
  position: fixed;
  display: none;
  top: 3rem;
  right: 3rem;
  width: 5rem;
  height: 4.5rem;
  background: rgba(255, 255, 255, 0.55);
  border-radius: 0.5rem;
  z-index: 1000;
  box-shadow: 0 7px 13px -3px rgba(50, 50, 93, 0.25),
      0 4px 8px -4px rgba(0, 0, 0, 0.3), 0 -3px 8px -3px rgba(0, 0, 0, 0.025);
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.burger::after {
  content: "MENU";
}

.burger span {
  width: 3rem;
  height: 0.33rem;
  background: rgb(0, 0, 0);
  margin: 0.25rem auto;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.nav-background-cover {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(50, 50, 50, 0);
  pointer-events: none;
  -webkit-transition: background 0.4s ease;
  transition: background 0.4s ease;
}
.nav-background-cover.open {
  pointer-events: all;
  background: rgba(50, 50, 50, 0.65);
}

@media screen and (max-width: 1000px) {
  body {
    overflow-x: hidden;
  }

  nav {
    position: fixed;
    top:0;
    background: rgba(255, 255, 255, 0);
    z-index: 1;
  }
  
  .nav-links {
    position: absolute;
    top: 0;
    right: 0;
    height: 100vh;
    padding: 30vh 0;
    width: 40%;
    display: flex;
    flex-direction: column;
    -webkit-transform: translate(20%);
            transform: translate(20%);
    opacity: 0;
    background: rgba(255, 255, 255, 0.7);
    -webkit-transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
    transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
    transition: transform 0.5s ease, opacity 0.5s ease;
    transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
    pointer-events: none;
  }
  .nav-links.open {
    -webkit-transform: translate(0%);
            transform: translate(0%);
    opacity: 1;
    pointer-events: all;
  }

  .nav-links .link {
    width: 100%;
  }

  .burger {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .burger.toggle {
    background: none;
    box-shadow: none;
  }

  .toggle .line1 {
    -webkit-transform: translateY(0.8rem) rotate(-45deg);
            transform: translateY(0.8rem) rotate(-45deg);
  }

  .toggle .line2 {
    opacity: 0;
  }

  .toggle .line3 {
    -webkit-transform: translateY(-0.8rem) rotate(45deg);
            transform: translateY(-0.8rem) rotate(45deg);
  }

  .nav-background-cover {
    display: block;
  }
}

@media screen and (max-width: 714px) {
  .burger {
    top: 1rem;
    right: 1rem;
  }

  .nav-links {
    width: 50%;
  }

  .nav-links .link {
    padding: 0;
    width: 100%;
  }
}
.item-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.item-block-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 2rem 0;
  border-top: 0.14rem solid rgb(230, 230, 230);
  border-bottom: 0.14rem solid rgb(230, 230, 230);
}

.list-toggle {
  height: 3rem;
  width: 3rem;
  align-self: flex-end;
  margin: 4rem 4rem 0 0;
  background: gray;
  cursor: pointer;
}

@media screen and (max-width: 714px) {
  .list-toggle {
    display: none;
    pointer-events: none;
  }
}

.item-block {
  padding: 4rem 0rem;
  width: 50%;
  border-bottom: 0.14rem solid rgb(230, 230, 230);
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

.item-block.inline {
  width: 100%;
}

.item-block.right {
  border-right: 0.14rem solid rgb(230, 230, 230);
}

.item-block.odd {
  border-right: 0;
}

.item-block.last {
  border-bottom: 0;
}

.item-block p {
  font-size: 1.3rem;
  color: rgb(100, 100, 100);
  line-height: 2.5rem;
  width: 80%;
}

.item-block .item-figure {
  width: 80%;
  display: flex;
  justify-content: center;
  height: 20vw;
  margin-bottom: 3rem;
  overflow: hidden;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.item-block .item-figure .item-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.item-block .item-figure:hover .item-img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media screen and (max-width: 1000px) {
  .item-block .item-figure {
    width: 60%;
    height: 30vw;
  }
  .item-block {
    width: 100%;
  }
  .item-block.right {
    border-right: 0;
  }

  .item-block.odd {
    border-right: 0;
  }
}

@media screen and (max-width: 714px) {
  .item-block .item-figure {
    width: 80%;
    height: 40vw;
  }
}

.main-container .main-title {
  margin: 0 2rem;
  margin-bottom: 3rem;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
  text-align: center;
  font-size: 6rem;
  font-weight: 700;
}

.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.main-container .sub-title {
  margin: 0 2rem;
  margin-bottom: 3rem;
  font-size: 2rem;
  text-align: center;
  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
  color: rgb(100, 100, 100);
}

.main-container .sub-title:hover {
  color: rgb(50, 200, 200);
}

.common-content {
  width: 80%;
  line-height: 2.5rem;
  font-size: 1.3rem;
  color: rgb(100, 100, 100);
}

.common-content a {
  -webkit-text-decoration: normal;
          text-decoration: normal;
  color: rgb(100, 100, 100);
}

.common-content .ul {
  list-style: none;
}

.common-content li li {
  margin-left: 2rem;
}

.common-content h1 {
  font-size: 3rem;
  margin: 3rem 0;
}

.common-content h2 {
  font-size: 2rem;
  margin-top: 3rem;
  margin-bottom: 1rem;
}

.sort-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.main-figure {
  width: 80%;
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
  overflow: hidden;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.sub-figure {
  height: 10rem;
  width: 10rem;
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 3rem;
  overflow: hidden;
  
}

.main-figure .main-img {
  width: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.main-figure:hover .main-img {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.sub-figure .sub-img {
  height: 8rem;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.sub-figure:hover .sub-img {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

@media screen and (max-width: 1000px) {
  .main-container .item-block-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    margin: 6rem 0;
    border-top: 0.14rem solid rgb(230, 230, 230);
    border-bottom: 0.14rem solid rgb(230, 230, 230);
  }
}

@media screen and (max-width: 714px) {
  .main-container .main-title {
    font-size: 4rem;
  }
}

